/* GLOBAL */
html * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    max-width: 100%;
}

body {
    /* background-color: #000000de; */
    background-image: linear-gradient(to bottom, #252525, #1d1d1d, #161616, #0d0d0d, #000000);
    background-attachment: fixed;
    font-family: 'Roboto', sans-serif;
}
  
/* HEADER */

nav a:hover {
    text-shadow: 0 0 4px #000000af;
    color: #8bb4ff;
    transition: .2s;
}
.header-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
header, main, footer {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

header{
    position: fixed;
    max-width: 100%;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    background-color: #000000ec;
    padding: .5rem 2rem;
    z-index: 2;
}



@media  (min-width: 600px) {
    .header-nav {
    width: 200px;
    flex-direction: row;
    justify-content: space-between;
    }
    header, main, footer {
        text-align: center;
        flex-direction: row;
        max-width: 100%;
        min-width: 100%;
        justify-content: center;
        align-items: center;
    }

    header{
        position: fixed;
        min-width: 100%;
        justify-content: space-between;
        padding: .5rem 2rem;
    }
    .header-nav, .link {
        margin-bottom: 0;
    }
}

.header-nav, .link {
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase;
    margin: .5rem auto;
    font-weight: bold;
    text-shadow: 0px 0px 12px rgba(128, 128, 128, 0.5),0px 0px 7px rgba(242, 242, 242, 0.5);
}
.header-logo img {
    width: 50px;
}

.nav-link {
    padding: .5rem;
}

.dropdown-toggle::after {
    display: none;
}
/* MAIN  */

.next-section {
    display: none;
}

@media (min-width: 600px) {
    .next-section {
        display: flex;
        border-radius: .8rem;
        padding: .4rem .2rem 0 .2rem;
        align-items: flex-end;
        transition: .4s;
    }
    
    #arrowDown {
        position: absolute;
        bottom: 2vh;
        right: 20vw;
        display: flex;
        background-color: rgba(146, 193, 255, 0.788);
        border-radius: .8rem;
        padding: .4rem .3rem .2rem .3rem;
        align-items: flex-end;
        transition: .4s;
    }
    
    #arrowDown:hover {
        background-color: rgba(58, 58, 58, 0.788);
    }
}

main {
    flex-direction: column;
}

.main-fv, .main-sv, .main-tv {
    display: flex;
    justify-content: center;
    align-items: center;
}

.main-fv {
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    height: 100vh;
    max-width: 100%;
    min-width: 100%;
    flex-direction: column;
}
.hero-container {
    flex-wrap: wrap;
    align-items: center;
    max-width: 100%;
    margin-top: 30vh;
    background-color: rgb(255 255 255 / 9%);
    padding: 2rem;
    border-radius: 0.5rem;
    box-shadow: 0px 0px 15px rgb(255 255 255 / 21%);
    margin-top: 2rem;
}
@media (min-width: 600px) {
    .hero-container {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        max-width: 100%;
        margin-top: 30vh;
        background-color: rgb(255 255 255 / 9%);
        padding: 4rem;
        border-radius: 0.5rem;
        box-shadow: 0px 0px 15px rgb(255 255 255 / 21%);
        margin-bottom: 1rem;
    }
}
.hero-h1 {
    color: #ffffff;
    text-shadow: 0px 0px 12px rgba(128, 128, 128, 0.5),0px 0px 7px rgba(242, 242, 242, 0.5), 0 0 2px #000000d5;
    margin: 0 .5rem;
}

.my {
    color: rgb(35 149 197);
}
.tine {
    color: rgb(255 221 38);
}
.rary {
    color: rgb(93 229 225);;
}
.hero-text {
    color: rgb(255, 255, 255);
    text-shadow: 1px 1px 4px #000000;
    background-color: rgba(0, 0, 0, 0.507);
    border-radius: 0.5rem;
    text-shadow: 0px 0px 12px rgba(128, 128, 128, 0.5), 0 0 4px #000000d5;
    padding: .5rem 1rem;
    font-size: 1.2rem;
}

.main-sv {
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    height: auto;
    min-width: 100%;
    max-width: 100%;
    flex-direction: column;
}

@media (min-width: 768px) {
    .main-sv {
        height: auto;
    }
}

.cta-cont {
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.call-to-action {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 30vh;
    min-width: 50%;
    justify-content: center;
    background-position: center;
    background-repeat: no-repeat;
    color: #ffffff;
    text-shadow: 0 0 3px #000000da;
    font-size: 1.2rem;
    border-radius: .5rem;
    padding: 1rem 3rem;
}
.btn-cta {
    color: #ffffff;
    text-shadow: 0 0 3px #0000008c;
    box-shadow: 1px 1px 5px #000000c7;
    transition: .5s;
}
.btn-cta:hover {
    color: #ffffff;
    translate: ease all .5s;
    text-shadow: 1px 1px 4px #0000008c;
    transform: scale(1.05);
    box-shadow: 2px 2px 5px #000000c7;
}

.plane img{
    position: relative;
    top: 13vh;
    
    max-width: 250px;
    z-index: 1;
    justify-content: flex-start;
}
@media (min-width: 800px) {
    .plane img {
        max-width: 25vw;
    }
}

/* CAROUSEL SWIPER */

.container-carousel {
    margin-bottom: 2rem;  
}

.city-cont {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin: 1rem 0;
}
.city-img {
    width: 75vw;
    height: 50vh;
    border-radius: 0.5rem;
    -webkit-border-radius: 0.5rem;
    -moz-border-radius: 0.5rem;
    -ms-border-radius: 0.5rem;
    -o-border-radius: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    box-shadow: 1px 1px 10px #000000ce;
}

.city-img:hover {
    transition: ease all .5s;
    transform: scale(1.04);
}

.city-img h3 {
    background-color: #000000a2;
    color: #ffffff;
    width: 100%;
    margin-bottom: 1rem;
}

@media (min-width: 780px) {
    .carousel-container {
        width: 95vw;
    }
    .city-img {
        width: 40vw;
        height: 40vh;
        border-radius: 0.5rem;
        -webkit-border-radius: 0.5rem;
        -moz-border-radius: 0.5rem;
        -ms-border-radius: 0.5rem;
        -o-border-radius: 0.5rem;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }
}

@media (min-width: 960px) {
    .carousel-container {
        width: 95vw;
    }
    .city-img {
        width: 40vw;
        height: 40vh;
        border-radius: 0.5rem;
        -webkit-border-radius: 0.5rem;
        -moz-border-radius: 0.5rem;
        -ms-border-radius: 0.5rem;
        -o-border-radius: 0.5rem;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }
}

@media (min-width: 1200px) {
    .carousel-container {
        width: 80vw;
        margin: 0 0 2rem 0;
    }
    .city-img {
        width: 35vw;
        height: 35vh;
        border-radius: 0.5rem;
        -webkit-border-radius: 0.5rem;
        -moz-border-radius: 0.5rem;
        -ms-border-radius: 0.5rem;
        -o-border-radius: 0.5rem;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }
}
.carousel-h2 {
    color: #ffffff;
    text-shadow: 0 0 5px rgba(128, 128, 128, 0.815), 0 0 3px #000000;
    background-color: #0000006b;
    border-radius: 1rem;
    width: auto;
    margin-bottom: 2rem;
    padding: .5rem 2rem;
}

.swiper-scrollbar-drag {
    background-color: #ffffff!important;
}
.swiper-button-prev::after{
    color: #ffffff;
}
.swiper-button-next::after {
    color: #ffffff;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active {
    background: #ffffff;
}
/* FOOTER */

footer {
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    background-color: #000000d5;
    color: #ffffff;
}

footer a {
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    margin-right: 1rem;
}
.cont-nav-socials {
    display: flex;
    min-width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin: .5rem auto;
}

@media (min-width: 600px) {
    .cont-nav-socials {
        flex-direction: row;
    }
}

.footer-nav {
    display: flex; 
    align-items: center;
    justify-content: space-between;
    width: 200px;
}


.socials  nav{
    display: flex;
    justify-content: space-around;
    width: 200px;
    margin: .5rem auto;
}

.socials img {
    width: 30px;
}

.footer-logo img {
    width: 40px;
}

.copy p {
    margin-bottom: .3rem;
}

/* CITIES PAGE */

.container-cities {
    padding: 0;
}
.spinner {
    width: 10rem;
}
a {
    text-decoration: none;
}
.cont {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
}
.cities-hero {
    height: 70vh;
    color: #ffffff;
    display: flex;
    justify-content: center;
    background-position: center;
    max-width: 100%;
    width: 100%;
    margin-bottom: 1rem;
    padding: 1rem;
}
.cities-hero h1 {
    margin-top: 10vh;
    margin-bottom: 15vh;
    background-color: #0000006b;
    padding: .4rem 2rem;
    border-radius: .6rem;
}
.hero-content-cities {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction: column;
        margin-bottom: 2rem;
}

.hero-content-cities input {
    font-size: 1.1rem;
    border-radius: .5rem;
    box-shadow: 1px 1px 4px #949494;
    padding: .3rem 1rem;
    border: none;
    outline: none;
}

.cities-img {
    height: 40vh;
    border-radius: 0.5rem;
    background-position: center;
    -webkit-border-radius: 0.5rem;
    -moz-border-radius: 0.5rem;
    -ms-border-radius: 0.5rem;
    -o-border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    box-shadow: 1px 1px 10px #000000ce;
}

.cities-img:hover {
    transition: ease all .5s;
    transform: scale(1.02);
}


.cities-img h3 {
    background-color: #000000a2;
    color: #ffffff;
    width: 100%;
    margin-bottom: .5rem;
    border-radius: .4rem;
}

.cities-h2 {
    color: #ffffff;
}
.more-info {
    padding: .2rem 2rem;
}
.more-info:hover {
    color: #fff2f2;
    transition: ease all .5s;
    text-shadow: 0 0 3px #000000;
    transform: scale(1.02);
}
.not-found {
    margin: 10vh auto;
    background-color: rgba(255, 0, 0, 0.199);
    border-radius: .4rem;
    width: 20rem;
    padding: .4rem;
    color: #ffffff;
}
/* CITY PAGE */

.hero-city {
    height: 60vh;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-position: center;
    max-width: 100%;
    width: 100%;
    margin-bottom: 1rem;
    padding: 1rem;
}
.hero-city h1{
    color: #ffffff;
}
.place-cont {
    background-color: #00000094;
    border-radius: .5rem;
    padding: .2rem 1rem;
}

.hero-city p{
    color: #ffffff;
}

.itinerary-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #6b6b6b;
    font-size: 1.4rem;
}

.underconst {
    background-color: rgba(255, 0, 0, 0.158);
    border-radius: .5rem;
    padding: .2rem 2rem;
    margin-top: 3rem;
}

/* ITINERARIES */
.itine-h2 {
    color: #ffffff;
    text-transform: uppercase;
}
.itineraries-container {
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 3px gray;
    width: 85vw;
    background: rgb(255,255,255);
    color: #e7e7e7;
    color: #505050;
    padding: 2rem 1rem .4rem 1rem;
    border-radius: .5rem;
}

.publisher {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.img-bg {
    padding: .14rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 40%;
    background-color: #ffc107;
    box-shadow: 0 0 .15rem #000000c7;
}
.img-bg img{
    height: 6rem;
    width: 6rem;
    background-repeat: no-repeat;
    background-position: 50%;
    border-radius: 40%;
    background-size: 100% auto;
}

.cont-likes-publisher {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.cont-info-it {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.it-info {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

@media (min-width: 1200px) {
    .it-info {
        flex-direction: row;
    }
    .itineraries-container{
        width: 80vw;
    }
}
@media (min-width: 1400px) {
    .itineraries-container{
        width: 60vw;
    }
}

.italic-shadow {
    text-shadow: 0 0 1px rgba(80, 80, 80, 0.925);
}

.h2-title-itine {
    background-color: #3e96df8f;
    display: inline-block;
    padding: 0.5rem 2rem;
    color: white;
    text-shadow: 0 0 0.2rem black;
    box-shadow: 1px 1px .2rem grey;
    border-radius: 1rem;
}

.no-itine h4{
    padding: 1.5rem 2rem 1.5rem 2rem;
    color: #ffffff;
    box-shadow: 2px 2px 4px #000000ce;
    border-radius: 2rem;
    text-shadow: 1px 1px 4px #000000ce;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.no-itine img {
    width: 300px;
}
.linkedin-logo img{
    width: 20px;
}
.accordion-button:not(.collapsed) {
    color: #2a2a2a;
    background-color: #f3f3f3;
}
.accordion-button:focus {
    border-color: #ffffff;
    box-shadow: none;
}

.likes {
    display: flex;
}
.cont-likes {
    margin-top: .8rem;
}
.like-heart {
    cursor: pointer;
    height: 28px;
    width: 28px;
}
/* ACTIVITIES */
.cont-act-comments {
    background-color: #ffffff;
}
.act-cont {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.activity-style {
    margin: .5rem;
    /* background-color: #e9ecef; */
    height: auto;
    padding: 1rem;
    color: #6b6b6b;
    text-shadow: 0 0 .15rem #3a3a3a;
    border-radius: .5rem;
    /* box-shadow: 0 0 .3rem #6e6e6ee1; */
}

.activity-style img{
    margin-top: .5rem;
    object-fit: cover;
    height: 250px;
    width: 300px;
    box-shadow: .05rem .05rem .4rem #6e6e6ee1;
    background-repeat: no-repeat;
    background-position: 50%;
    border-radius: .6rem;
    background-size: 100% auto;

}

.activity-style h4 {
    max-width: 200px;
    margin: auto;
    height: 50px;
}
/* COMMENTS */

.all-comments-container {
    height: 250px;
    width: 400px;
    max-width: 100%;
    overflow-y: scroll;
    font-size: 1.2rem;
    padding: 1rem 1rem 0 1rem;
    border-radius:.9rem .9rem 0 0;
    box-shadow: 0px 0px .15rem gray;
    color: #000000;
    background-color: #eeeeee;
}
.comment {
  width: fit-content;
  font-size: 1rem;
  max-width: 100%;
  border-radius: 0 1rem 1rem 1rem;
  padding: .3rem 1rem .3rem .3rem;
  background-color: rgb(255, 255, 255);
  margin-left: .4rem;
  margin-bottom: .6rem;
  box-shadow: 1px 1px 4px #979797
}

.comment-content {
    min-height: 25px;
    max-width: 95%;
    padding-left: 0.8rem;
}


.all-comments-container::-webkit-scrollbar {
    -webkit-appearance: none;
}

.all-comments-container::-webkit-scrollbar:vertical {
    min-width: 10px;
}

.all-comments-container::-webkit-scrollbar-button:increment, .all-comments-container::-webkit-scrollbar-button {
    display: none;
} 

.all-comments-container::-webkit-scrollbar-thumb {
    background-color: #797979;
    border-radius: 20px;
}

.all-comments-container::-webkit-scrollbar-track {
    border-radius: 10px;  
}

.x-alineator{
    display: flex;
}

.authorComment-img {
  width: 35px;
  height: 35px;
  object-fit: cover;
  border-radius: .9rem;
  box-shadow: 0px 0px 5px #b8b8b8;
  background-position: center;
  background-size: cover;
}

.authorComment-img:hover {
  cursor: pointer;
}

.commentContent-container{
  border-radius: .6rem;
  text-align: start;
  margin-left: .6rem;
}

.comment-author {
  margin-bottom: 0px;
  color: #6d6d6d;
}

.comment-author:hover {
  cursor: pointer;
  color: #080861d5;
}


.input-container {
    display: flex;
    font-size: 1rem;
    max-width: 400px;
    height: auto;
    width: 100%;
    flex-direction: column;
}

.input-container input {
    width: 400px;
    max-width: 100%;
    outline: none;
    border: none;
    padding: .4rem 1rem .4rem 3rem;
    border-radius: 0 0 .5rem .5rem;
    box-shadow: 0px 0px .1rem rgb(87, 87, 87);
    background-color: #ffffff;
}

.paperPlane-icon{
    position: relative;
    left: -0.5rem;
    height: 10;
    top: -1.65rem;
    width: 50px;
    cursor: pointer;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid #858585;
    color: #898996;
}

.paperPlane-icon:hover {
    color: rgb(23, 104, 23);
}

.editingInput {
    font-size: 1rem;
    width: 200px;
    border-radius: .7rem;
    padding: .3rem;
    outline: none;
    border: 1px solid #4b4b4b;
}
.displayNone {
    display: none;
}

.edit-icon {
    width: 1rem;
    margin-left: .4rem;
    transition: .3s;
}

.edit-icon:hover {
    color: #198b19;
    cursor: pointer;
    transition: .3s;
}

.delete-icon {
    width: .8rem;
    transition: .3s;
    margin-left: .4rem;
}

.delete-icon:hover {
    color: #ff1616;
    transition: .3s;
    cursor: pointer;
}
.modifyCommentIcons-container {
    display: flex;
    font-size: 1.1rem;
    align-self: center;
}
.sendEditedIcon {
    font-size: 1.2rem;
    margin-left: .4rem;
}

.sendEditedIcon:hover {
    cursor: pointer;
}


/* SIGN UP  LOG IN */

.signup-container {
    display: flex;
    justify-content: center;
    min-height: 90.8;
}
.signup-container h1 {
    color: #6e6e6e;
    text-shadow: 1px 1px 4px #4d4d4d6b;   
}
.cont-form {
    margin-top: 8rem;
    margin-bottom: 2rem;
    background-color: #ffffff;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 2px 2px 5px #000000;
    color: #6e6e6e;
}

@media (min-width: 960px) {
    .signup-container {
        height: 90.8vh;
    }
    .cont-form {
        margin-top: 3rem;
        padding: 2rem 5rem;
    }
