.form-style {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

form input {
    padding: .5rem 1rem;
    border: none;
    margin-bottom: .8rem;
    max-width: 16rem;
    width: 16rem;
    box-shadow: 1px 1px 4px #4d4d4d6b;
    border-radius: .5rem;
    outline: none;
}

button img{
    text-shadow: 0 0 2px #000000;
}
select {
    outline: none;
    border: none;
    border-radius: .5rem;
    max-width: 16rem;
    margin-bottom: .5rem;
    background-color: #ffffff;
    box-shadow: 1px 1px 4px #4d4d4d6b;
    padding: .5rem;
    color: #6e6e6e;
}


.btns { 
    color: #ffffff;
    text-shadow: 0 0 2px #000000;
    background-color: #1bd3b1;
    margin: 0;
    padding: .5rem 1rem;
    border: none;
    margin-bottom: .5rem;
    box-shadow: 1px 1px 4px #4d4d4d6b;
    border-radius: .5rem;
    outline: none;
    display: flex;
    align-items: center;
}

.btns:hover { 
    background-color: #26e9c5;
    text-shadow: 0 0 2px #000000;
}

.img-user {
    width: 50px;
    height: 50px;
